<style lang="less" module>
.firstPage {
}
</style>
<template>
  <div :class="$style.firstPage">
    <div
      id="model1"
      style="border:1px solid red; width:900px;height:700px;"
    ></div>
    <div
      id="WebGL-output"
      style="border:1px solid red; width:900px;height:700px;"
    ></div>
  </div>
</template>
<script>
import * as THREE from 'three';
import { FBXLoader } from 'three/examples/jsm/loaders/FBXLoader';
// import {} from '@/api'
export default {
    name: 'FirstPage',
    data() {
        return {
            scene: null,
            camera: null,
            renderer: null
        };
    },
    computed: {},
    watch: {},
    mounted() {
        this.test2();
        // this.$refs.Model1.append(this.Wave.renderer.domElement);
    },
    methods: {
        test(domId = 'WebGL-output') {
            // 
            let container = document.getElementById(domId);
            this.scene = new THREE.Scene();
            // this.scene.background = new THREE.Color(0x050505);
            // 
            this.camera = new THREE.PerspectiveCamera(60, container.clientWidth / container.clientHeight, 0.1, 10000);

            // 0,0,0
            this.camera.position.x = 0;
            this.camera.position.y = 0;
            this.camera.position.z = 5000;
            // this.camera.lookAt(this.scene.position);

            // WebGL
            this.renderer = new THREE.WebGLRenderer();
            this.renderer.setClearColor(new THREE.Color(0xeeeeee));
            this.renderer.setSize(container.clientWidth, container.clientHeight);

            this.scene.add(new THREE.AmbientLight('#ffffff'));

            var loader = new FBXLoader();
            let that = this;
            loader.load('/3dModels/wave.fbx', object => {
                that.scene.add(object);
                that.animate();
                // 
                // this.renderer.render(scene, camera);
            });

            // 
            container.appendChild(this.renderer.domElement);
        },
        test2(domId = 'model1') {
            let container = document.getElementById(domId);
            // 
            var scene = new THREE.Scene();

            // 
            var camera = new THREE.PerspectiveCamera(45, container.clientWidth / container.clientHeight, 0.1, 1000);

            // 0,0,0
            camera.position.x = -30;
            camera.position.y = 40;
            camera.position.z = 30;
            camera.lookAt(scene.position);

            // WebGL
            var renderer = new THREE.WebGLRenderer();
            renderer.setClearColor(new THREE.Color(0xeeeeee));
            renderer.setSize(container.clientWidth, container.clientHeight);

            // 
            var axes = new THREE.AxisHelper(20);
            scene.add(axes);

            // 
            var planeGeometry = new THREE.PlaneGeometry(60, 20);
            // 0xcccccc
            var planeMaterial = new THREE.MeshBasicMaterial({ color: 0xcccccc });
            var plane = new THREE.Mesh(planeGeometry, planeMaterial);

            // 
            plane.rotation.x = -0.5 * Math.PI;
            plane.position.x = 15;
            plane.position.y = 0;
            plane.position.z = 0;

            // 
            scene.add(plane);

            // 
            var cubeGeometry = new THREE.BoxGeometry(4, 4, 4);
            // wireframetrue
            var cubeMaterial = new THREE.MeshBasicMaterial({ color: 0xff0000, wireframe: true });
            var cube = new THREE.Mesh(cubeGeometry, cubeMaterial);

            // 
            cube.position.x = -4;
            cube.position.y = 3;
            cube.position.z = 0;

            // 
            scene.add(cube);

            // 
            var sphereGeometry = new THREE.SphereGeometry(4, 20, 20);
            // wireframetrue
            var sphereMaterial = new THREE.MeshBasicMaterial({ color: 0x7777ff, wireframe: true });
            var sphere = new THREE.Mesh(sphereGeometry, sphereMaterial);

            // 
            sphere.position.x = 20;
            sphere.position.y = 4;
            sphere.position.z = 2;

            // 
            scene.add(sphere);

            // 
            document.getElementById('model1').appendChild(renderer.domElement);

            // 
            renderer.render(scene, camera);
        },
        animate() {
            this.renderer.render(this.scene, this.camera);

            requestAnimationFrame(this.animate);
        }
    }
};
</script>
